var test = require('ava');
var app = require('../app');
var request = require('supertest-promised')(app);

test.serial('first connection', connect);
test.serial('repetitive connection', connect);

// test.serial('first close', close);
// test.serial('repetitive close', close);

function connect(t) {
  return request.post('/')
    .send({
      "host": "113.172.82.132",
      "user": "support",
      "pass": "support",
      "tunport": 1080
    })
    .expect(200)
    .expect(function (res) {
      t.is(res.body.status, 'Connected');
    });
}

function close(t) {
  return request.post('/')
    .send({
      host: "113.172.82.132",
      tunport: 1080,
      close: 1
    })
    .expect(200)
    .expect(function (res) {
      t.is(res.body.status, 'Connection closed');
    });
}
